.ContactUs-pageheader{
    margin-top: 35px;
    text-align: center
}
.ContactUs-pageheader h1{
    font-size: 2rem;
    line-height: 1.5rem;
    font-weight: bold;
    color: #454545;
    padding-top: 5px;
}
.ContactUs-pageheader .breadcrumb{
    justify-content: center;
    background-color: transparent;
    margin-bottom: 10px;
    display: flex;
    padding: 0.75rem 1rem;
    list-style: none;
    border-radius: 0.25rem;
}
.ContactUs-pageheader .breadcrumb .breadcrumb-item{
    font-size: 1rem;
    line-height: 1.5rem;
    color: #454545;
    text-transform: capitalize;
}
.ContactUs-pageheader .breadcrumb .breadcrumb-item a{
    color: #454545;
    font-size: 1rem;
}
.ContactUs-pageheader .breadcrumb .breadcrumb-item a:hover{
    color: black;
    text-decoration: none;
}
.ContactUs-pageheader .breadcrumb .active::before{
    content: ">";
}
.ContactUs-content{
    background-image: url("https://halongcomplex.sunworld.vn/wp-content/themes/halongcomplex/dist/images/background.png");
    background-size: cover;
    background-position: center;
    background-color: #f1f5f7;
    background-repeat: no-repeat;
    background-attachment: fixed;
    padding: 40px 0;
}
.ContactUs-content .right-content .khungtrinhbay .nut .send:hover{
    text-decoration: none;
}
label.error.fail-alert{
    color: red;
    line-height: 1;
    padding: 2px 0 6px 6px;
}
.ContactUs-content .left-content .table tbody tr td{
   border: none;
}